<!-- eslint-disable -->
<template>
    <div class="content-wrapper">
        <section class="content">
            <div class="container-fluid">
                <div class="container">
                    <div class="def_home">
                        <div class="app-page">
                            <div class="head">
                                <h3>Swimming Workouts</h3>
                            </div>
                            <div class="workouts item" v-for="(item,i) in workouts" :key="i">
                                <div class="view">
                                    <img :src="images(item.image)">
                                    <div>{{item.name}}</div>
                                </div>
                                <div class="option-i">
                                    <div @click="remove({table:'workouts',id:item.id,i:i})" class="edit">
                                        <i class="far fa-trash-alt"></i>
                                        <span>Remove</span>
                                    </div>
                                    <router-link tag="div" class="edit" :to="params(item.id)">
                                        <i class="fas fa-edit"></i>
                                        <span>Edit</span>
                                    </router-link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<!-- eslint-disable -->

<script>
    import {
        mapState
    } from 'vuex'
    import {
        mapGetters
    } from 'vuex'
    import {
        mapMutations
    } from 'vuex'
    export default {
        created() {
            this.API(`get.php?action=workouts`, (data) => {
                if (data) {
                    this.modalContainer(false);
                    this.workoutsArr(data);

                }
            });
        },
        data() {
            return {

            }
        },
        computed: {
            ...mapState([
                'workouts',
                'APIURL'
            ]),
            ...mapGetters([
                'API'
            ])
        },
        props: [],
        updated: function () {},
        components: {},
        watch: {

        },
        methods: {
            params(id) {
                return {
                    name: 'updates',
                    params: {
                        id: id,
                        name: this.$route.name
                    }
                }
            },
            ...mapMutations([
                'modalContainer',
                'deleteAjax',
                'workoutsArr'

            ]),
            images(url) {
                url = JSON.parse(url);
                return this.APIURL + url[0];
            },
            remove(obj) {
                this.modalContainer("remove");
                this.deleteAjax(obj);

            },
        }
    }
</script>
<style>

.app-page {
    margin: 0 20px;
    background: #fff;
    padding: 20px;
}
.app-page .app-page-content {
    border: 1px solid #ddd;
}
.app-page .head{
    padding-bottom: 20px
}
.workouts.item .view {
    background: mintcream;
    padding: 10px;
    border: 1px solid #ddd;
    width: 160px;
  
}
.workouts.item {
    background: white;
    border: 1px solid #ddd;
    padding: 20px;
    vertical-align: top
}
.container{
    background: white
}
.workouts.item .view div {
    border-top: 1px solid mediumblue;
    padding-top: 15px;
    font-weight: 900;
    font-size: 14px
}
.workouts.item .view img {
    padding-bottom: 15px;
    width: 120px;
}
.workouts.item {
    display: inline-block;
    background: mintcream;
    text-align: center;
    padding: 10px;
    border: 1px solid #ddd;
    margin: 0 5px
}
.workouts.item .edit {
    padding-top: 20px;
    text-align: center;
    cursor: pointer;
}
.workouts.item .edit * {
    vertical-align: middle;
    font-size: 13px;
    color: magenta;
    margin: 0 2.5px;
}
.workouts.item .option-i div {
    display: inline-block;
    width: 50%
}
@media screen and (max-width: 600px) {
.app-page {
        padding: 0;
        margin: 0;
}
.workouts.item {
        background: white;
        border: 1px solid #ddd;
        padding: 20px;
        display: block;
        margin: 10px 0;
}
.workouts.item .view img {

        width: 100% !important;
}
.workouts.item .view {

        width: 100% !important;
}
}

</style>